
#site-header {
    color: #dfe5ec;
    float: left;
    height: 60px;
    line-height: 60px;
    margin: 0;
    padding: 0 20px;
    position: relative;
    box-sizing: border-box;
    border-bottom: 5px solid transparent;
    font-family: 'Righteous', cursive;
    font-size: 1.5rem;
}
.el-menu-item {
    font-size: 1.1rem !important;
    font-family: 'Roboto', sans-serif;
}
.is-active{
    color: #00d1b2 !important;
}



